'use client';

import React, { useState } from 'react';
import axios from 'axios';

// API-Basis-URL
const API_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8080';

interface EnrichmentPanelProps {
  url: string;
  title: string;
  description?: string;
  onApplyTags: (tags: string[]) => void;
  onApplyCategory: (category: string) => void;
  onApplyDescription: (description: string) => void;
}

const EnrichmentPanel: React.FC<EnrichmentPanelProps> = ({
  url,
  title,
  description,
  onApplyTags,
  onApplyCategory,
  onApplyDescription
}) => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [result, setResult] = useState<any>(null);
  const [expanded, setExpanded] = useState(false);
  const [youtubeUrl, setYoutubeUrl] = useState('');
  const [youtubeLoading, setYoutubeLoading] = useState(false);
  const [youtubeError, setYoutubeError] = useState<string | null>(null);

  const enrichBookmark = async () => {
    setLoading(true);
    setError(null);
    setResult(null);

    try {
      console.log('Sende Anreicherungsanfrage für:', { url, title, description });
      
      const response = await axios.post(
        `${API_URL}/api/enrichment/bookmark`,
        {
          url,
          title,
          description
        },
        {
          timeout: 30000 // 30 Sekunden Timeout
        }
      );
      
      console.log('Anreicherungsergebnis:', response.data);
      
      if (response.data.success) {
        setResult(response.data.data);
      } else {
        setError(response.data.message || 'Fehler bei der Anreicherung');
      }
    } catch (err: any) {
      console.error('Fehler bei der Anreicherung:', err);
      
      if (err.code === 'ERR_NETWORK') {
        setError('Netzwerkfehler: Server nicht erreichbar. Bitte prüfen Sie, ob der Server auf Port 8080 läuft.');
      } else if (err.response) {
        setError(err.response?.data?.message || `Fehler (${err.response.status}): ${err.response.statusText}`);
      } else {
        setError('Fehler bei der Anreicherung: ' + err.message);
      }
    } finally {
      setLoading(false);
    }
  };

  const generateYoutubeDescription = async () => {
    if (!youtubeUrl) {
      setYoutubeError('Bitte geben Sie einen YouTube-Link ein');
      return;
    }

    setYoutubeLoading(true);
    setYoutubeError(null);

    try {
      const response = await axios.post(
        `${API_URL}/api/enrichment/youtube-description`,
        {
          youtubeUrl
        },
        {
          timeout: 45000 // 45 Sekunden Timeout für YouTube-Verarbeitung
        }
      );

      if (response.data.success) {
        // Wenn erfolgreich, setze das Ergebnis und wende die Beschreibung an
        setResult(response.data.data);
        if (response.data.data.summary) {
          onApplyDescription(response.data.data.summary);
        }
      } else {
        setYoutubeError(response.data.message || 'Fehler bei der Verarbeitung des YouTube-Links');
      }
    } catch (err: any) {
      console.error('Fehler bei der YouTube-Verarbeitung:', err);
      
      if (err.code === 'ERR_NETWORK') {
        setYoutubeError('Netzwerkfehler: Server nicht erreichbar.');
      } else if (err.response) {
        setYoutubeError(err.response?.data?.message || `Fehler (${err.response.status}): ${err.response.statusText}`);
      } else {
        setYoutubeError('Fehler bei der Verarbeitung: ' + err.message);
      }
    } finally {
      setYoutubeLoading(false);
    }
  };

  return (
    <div className="mt-4 border border-gray-200 rounded-lg overflow-hidden bg-white shadow-sm">
      <div 
        className="bg-gradient-to-r from-indigo-50 to-purple-50 p-4 cursor-pointer flex justify-between items-center"
        onClick={() => setExpanded(!expanded)}
      >
        <h3 className="text-lg font-medium text-indigo-700 flex items-center">
          <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z"></path>
          </svg>
          KI-Anreicherung
        </h3>
        <div className="flex items-center">
          {!result && !loading && !error && (
            <button
              type="button"
              onClick={(e) => {
                e.stopPropagation();
                enrichBookmark();
              }}
              className="mr-3 px-3 py-1 bg-indigo-600 text-white text-sm rounded-md hover:bg-indigo-700 transition-colors"
            >
              Jetzt anreichern
            </button>
          )}
          <svg 
            className={`w-5 h-5 text-indigo-600 transition-transform duration-200 ${expanded ? 'transform rotate-180' : ''}`} 
            fill="none" 
            stroke="currentColor" 
            viewBox="0 0 24 24" 
            xmlns="http://www.w3.org/2000/svg"
          >
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M19 9l-7 7-7-7"></path>
          </svg>
        </div>
      </div>
      
      {expanded && (
        <div className="p-4">
          {/* YouTube-Link-Eingabefeld */}
          <div className="mb-4 p-4 bg-red-50 rounded-lg border border-red-100">
            <h4 className="text-md font-medium text-red-700 mb-2 flex items-center">
              <svg className="w-5 h-5 mr-1" fill="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                <path d="M19.615 3.184c-3.604-.246-11.631-.245-15.23 0-3.897.266-4.356 2.62-4.385 8.816.029 6.185.484 8.549 4.385 8.816 3.6.245 11.626.246 15.23 0 3.897-.266 4.356-2.62 4.385-8.816-.029-6.185-.484-8.549-4.385-8.816zm-10.615 12.816v-8l8 3.993-8 4.007z"></path>
              </svg>
              YouTube-Beschreibung generieren
            </h4>
            <div className="mt-2">
              <input
                type="text"
                value={youtubeUrl}
                onChange={(e) => setYoutubeUrl(e.target.value)}
                placeholder="Fügen Sie einen YouTube-Link ein"
                className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-red-500"
              />
              {youtubeError && (
                <p className="mt-1 text-sm text-red-600">{youtubeError}</p>
              )}
              <div className="mt-2 flex justify-end">
                <button
                  type="button"
                  onClick={generateYoutubeDescription}
                  disabled={youtubeLoading}
                  className="px-3 py-1 bg-red-600 text-white text-sm rounded-md hover:bg-red-700 transition-colors disabled:bg-gray-400"
                >
                  {youtubeLoading ? (
                    <span className="flex items-center">
                      <svg className="animate-spin -ml-1 mr-2 h-4 w-4 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                        <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                        <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                      </svg>
                      Verarbeite...
                    </span>
                  ) : (
                    "Beschreibung generieren"
                  )}
                </button>
              </div>
            </div>
          </div>

          {loading && (
            <div className="flex items-center justify-center py-8">
              <div className="animate-spin rounded-full h-10 w-10 border-t-2 border-b-2 border-indigo-600"></div>
              <p className="ml-3 text-gray-600">Anreicherung läuft... Dies kann bis zu 20 Sekunden dauern.</p>
            </div>
          )}
          
          {error && (
            <div className="p-4 bg-red-50 border-l-4 border-red-500 text-red-700 rounded mb-4">
              <div className="flex">
                <svg className="h-5 w-5 text-red-500 mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12" />
                </svg>
                <p>{error}</p>
              </div>
              <div className="mt-3">
                <button
                  type="button"
                  onClick={enrichBookmark}
                  className="px-3 py-1 bg-red-600 text-white text-sm rounded-md hover:bg-red-700 transition-colors"
                >
                  Erneut versuchen
                </button>
              </div>
            </div>
          )}
          
          {result && (
            <div className="space-y-4">
              {/* Zusammenfassung */}
              {result.summary && (
                <div className="mb-4">
                  <div className="flex justify-between items-center mb-2">
                    <h4 className="text-md font-medium text-gray-700">Zusammenfassung</h4>
                    <button
                      type="button"
                      onClick={() => onApplyDescription(result.summary)}
                      className="px-3 py-1 text-xs bg-indigo-100 text-indigo-700 rounded-md hover:bg-indigo-200 transition-colors"
                    >
                      Übernehmen
                    </button>
                  </div>
                  <div className="p-3 bg-gray-50 rounded-md text-gray-700 text-sm">
                    {result.summary}
                  </div>
                </div>
              )}
              
              {/* Kategorie */}
              {result.suggestedCategory && (
                <div className="mb-4">
                  <div className="flex justify-between items-center mb-2">
                    <h4 className="text-md font-medium text-gray-700">Vorgeschlagene Kategorie</h4>
                    <button
                      type="button"
                      onClick={() => onApplyCategory(result.suggestedCategory)}
                      className="px-3 py-1 text-xs bg-indigo-100 text-indigo-700 rounded-md hover:bg-indigo-200 transition-colors"
                    >
                      Übernehmen
                    </button>
                  </div>
                  <div className="inline-block px-3 py-1 bg-indigo-100 text-indigo-800 rounded-full text-sm">
                    {result.suggestedCategory}
                  </div>
                </div>
              )}
              
              {/* Tags */}
              {result.suggestedTags && result.suggestedTags.length > 0 && (
                <div className="mb-4">
                  <div className="flex justify-between items-center mb-2">
                    <h4 className="text-md font-medium text-gray-700">Vorgeschlagene Tags</h4>
                    <button
                      type="button"
                      onClick={() => onApplyTags(result.suggestedTags)}
                      className="px-3 py-1 text-xs bg-indigo-100 text-indigo-700 rounded-md hover:bg-indigo-200 transition-colors"
                    >
                      Alle übernehmen
                    </button>
                  </div>
                  <div className="flex flex-wrap gap-2">
                    {result.suggestedTags.map((tag: string, index: number) => (
                      <div key={index} className="px-3 py-1 bg-gray-100 text-gray-800 rounded-full text-sm">
                        {tag}
                      </div>
                    ))}
                  </div>
                </div>
              )}
              
              {/* Key Insights */}
              {result.keyInsights && result.keyInsights.length > 0 && (
                <div className="mb-4">
                  <h4 className="text-md font-medium text-gray-700 mb-2">Wichtige Erkenntnisse</h4>
                  <ul className="list-disc pl-5 space-y-1 text-sm text-gray-700">
                    {result.keyInsights.map((insight: string, index: number) => (
                      <li key={index}>{insight}</li>
                    ))}
                  </ul>
                </div>
              )}
              
              {/* YouTube-spezifische Informationen */}
              {result.youtubeInfo && (
                <div className="mb-4 p-4 bg-red-50 rounded-lg border border-red-100">
                  <h4 className="text-md font-medium text-red-700 mb-2 flex items-center">
                    <svg className="w-5 h-5 mr-1" fill="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                      <path d="M19.615 3.184c-3.604-.246-11.631-.245-15.23 0-3.897.266-4.356 2.62-4.385 8.816.029 6.185.484 8.549 4.385 8.816 3.6.245 11.626.246 15.23 0 3.897-.266 4.356-2.62 4.385-8.816-.029-6.185-.484-8.549-4.385-8.816zm-10.615 12.816v-8l8 3.993-8 4.007z"></path>
                    </svg>
                    YouTube-Informationen
                  </h4>
                  <div className="space-y-2 text-sm">
                    {result.youtubeInfo.channelName && (
                      <p><span className="font-medium">Kanal:</span> {result.youtubeInfo.channelName}</p>
                    )}
                    {result.youtubeInfo.publishedAt && (
                      <p><span className="font-medium">Veröffentlicht:</span> {new Date(result.youtubeInfo.publishedAt).toLocaleDateString()}</p>
                    )}
                    {result.youtubeInfo.viewCount && (
                      <p><span className="font-medium">Aufrufe:</span> {parseInt(result.youtubeInfo.viewCount).toLocaleString()}</p>
                    )}
                    {result.youtubeInfo.duration && (
                      <p><span className="font-medium">Dauer:</span> {result.youtubeInfo.duration}</p>
                    )}
                  </div>
                </div>
              )}
              
              <div className="mt-3 pt-3 border-t border-gray-200">
                <button
                  type="button"
                  onClick={enrichBookmark}
                  className="px-3 py-1 bg-gray-200 text-gray-700 text-sm rounded-md hover:bg-gray-300 transition-colors"
                >
                  Erneut anreichern
                </button>
              </div>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default EnrichmentPanel; 