'use client';

import React, { useEffect, useState, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '../../../context/AuthContext';
import axios from 'axios';
import EnrichmentPanel from '../../../components/EnrichmentPanel';

// API-Basis-URL
const API_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8080';

type Category = {
  id: string;
  name: string;
};

type Tag = {
  id: string;
  name: string;
};

const NewBookmarkPage = () => {
  const router = useRouter();
  const { isAuthenticated, loading } = useAuth();
  const [formData, setFormData] = useState<{
    url: string;
    title: string;
    description: string;
    categoryId: string;
    tags: string[];
  }>({
    url: '',
    title: '',
    description: '',
    categoryId: '',
    tags: []
  });
  const [formErrors, setFormErrors] = useState<{
    url: string;
    title: string;
  }>({
    url: '',
    title: ''
  });
  const [categories, setCategories] = useState<Category[]>([]);
  const [allTags, setAllTags] = useState<Tag[]>([]);
  const [newTag, setNewTag] = useState('');
  const [submitting, setSubmitting] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [message, setMessage] = useState<string | null>(null);
  const [newTagName, setNewTagName] = useState('');
  const [tags, setTags] = useState<Tag[]>([]);
  const [autoEnrichEnabled, setAutoEnrichEnabled] = useState(true);
  const [enrichmentLoading, setEnrichmentLoading] = useState(false);
  const [enrichmentResult, setEnrichmentResult] = useState<any>(null);
  const [urlDebounceTimer, setUrlDebounceTimer] = useState<NodeJS.Timeout | null>(null);
  const [retryCount, setRetryCount] = useState(0);

  // Weiterleitung zur Login-Seite, wenn nicht authentifiziert
  useEffect(() => {
    if (!loading && !isAuthenticated) {
      router.push('/login');
    }
  }, [isAuthenticated, loading, router]);

  // Kategorien und Tags beim Laden der Seite abrufen
  useEffect(() => {
    if (isAuthenticated) {
      fetchCategories();
      fetchTags();
    }
  }, [isAuthenticated]);

  const fetchCategories = async () => {
    try {
      const response = await axios.get(`${API_URL}/api/categories`);
      setCategories(response.data.data);
    } catch (err) {
      console.error('Fehler beim Abrufen der Kategorien:', err);
    }
  };

  const fetchTags = async () => {
    try {
      const response = await axios.get(`${API_URL}/api/tags`);
      setTags(response.data.data);
      setAllTags(response.data.data);
    } catch (err) {
      console.error('Fehler beim Abrufen der Tags:', err);
    }
  };

  const isValidUrl = (url: string) => {
    try {
      new URL(url);
      return true;
    } catch (e) {
      return false;
    }
  };

  // Automatische Anreicherung mit Wiederholungsversuchen
  const autoEnrichBookmark = useCallback(async () => {
    // Nur anreichern, wenn URL gültig ist und autoEnrich aktiviert ist
    if (formData.url && isValidUrl(formData.url) && autoEnrichEnabled && !enrichmentLoading) {
      setEnrichmentLoading(true);
      try {
        console.log('Starte automatische Anreicherung für URL:', formData.url);
        const response = await axios.post(`${API_URL}/api/enrichment/bookmark`, {
          url: formData.url,
          title: formData.title || 'Unbenannt'
        }, {
          timeout: 30000 // 30 Sekunden Timeout
        });
        
        const result = response.data.data;
        console.log('Anreicherungsergebnis:', result);
        setEnrichmentResult(result);
        setRetryCount(0); // Zurücksetzen bei Erfolg
        
        // Automatisch Titel und Beschreibung übernehmen
        if (!formData.title.trim()) {
          setFormData(prev => ({
            ...prev,
            title: result.title || result.suggestedTitle || 'Unbenannt'
          }));
        }
        
        if (!formData.description.trim()) {
          setFormData(prev => ({
            ...prev,
            description: result.summary || ''
          }));
        }
        
        // Automatisch Kategorie und Tags übernehmen
        if (result.suggestedCategory) {
          handleApplyCategory(result.suggestedCategory);
        }
        
        if (result.suggestedTags && result.suggestedTags.length > 0) {
          handleApplyTags(result.suggestedTags);
        }
      } catch (err: any) {
        console.error('Fehler bei der automatischen Anreicherung:', err);
        
        // Automatischer Wiederholungsversuch bei Netzwerkfehlern
        if ((err.code === 'ERR_NETWORK' || err.code === 'ECONNABORTED') && retryCount < 3) {
          setRetryCount(prev => prev + 1);
          setTimeout(() => {
            autoEnrichBookmark();
          }, 2000 * retryCount); // Exponentielles Backoff
        }
      } finally {
        setEnrichmentLoading(false);
      }
    }
  }, [formData.url, formData.title, autoEnrichEnabled, enrichmentLoading, retryCount]);

  // Automatische Anreicherung, wenn URL geändert wird
  useEffect(() => {
    // Verzögerung hinzufügen, um nicht bei jedem Tastendruck eine Anfrage zu senden
    if (urlDebounceTimer) {
      clearTimeout(urlDebounceTimer);
    }
    
    const timer = setTimeout(() => {
      autoEnrichBookmark();
    }, 1000);
    
    setUrlDebounceTimer(timer);
    
    return () => {
      if (urlDebounceTimer) {
        clearTimeout(urlDebounceTimer);
      }
    };
  }, [formData.url, autoEnrichEnabled, autoEnrichBookmark]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
    
    // Validierung
    if (name === 'url') {
      if (!value.trim()) {
        setFormErrors(prev => ({ ...prev, url: 'Die URL ist erforderlich' }));
      } else if (!isValidUrl(value)) {
        setFormErrors(prev => ({ ...prev, url: 'Bitte gib eine gültige URL ein' }));
      } else {
        setFormErrors(prev => ({ ...prev, url: '' }));
      }
    }
    
    if (name === 'title') {
      if (!value.trim()) {
        setFormErrors(prev => ({ ...prev, title: 'Der Titel ist erforderlich' }));
      } else {
        setFormErrors(prev => ({ ...prev, title: '' }));
      }
    }
  };

  const handleTagSelect = (tagId: string) => {
    setFormData(prev => {
      if (prev.tags.includes(tagId)) {
        return { ...prev, tags: prev.tags.filter(id => id !== tagId) };
      } else {
        return { ...prev, tags: [...prev.tags, tagId] };
      }
    });
  };

  const handleCreateTag = async () => {
    if (!newTagName.trim()) return;
    
    try {
      const response = await axios.post(`${API_URL}/api/tags`, { name: newTagName });
      const createdTag = response.data.data;
      
      // Aktualisiere die Tags-Liste
      setTags(prevTags => [...prevTags, createdTag]);
      setAllTags(prevTags => [...prevTags, createdTag]);
      
      // Füge den neuen Tag zur Auswahl hinzu
      setFormData(prev => ({
        ...prev,
        tags: [...prev.tags, createdTag.id]
      }));
      
      // Setze das Eingabefeld zurück
      setNewTagName('');
    } catch (err: any) {
      console.error('Fehler beim Erstellen des Tags:', err);
      setError(err.response?.data?.message || 'Fehler beim Erstellen des Tags');
    }
  };

  const handleApplyTags = (suggestedTags: string[]) => {
    // Erstelle neue Tags, wenn sie noch nicht existieren
    const applyTags = async () => {
      const createdTags: { id: string; name: string }[] = [];
      
      for (const tagName of suggestedTags) {
        // Prüfe, ob der Tag bereits existiert
        const existingTag = tags.find(t => t.name.toLowerCase() === tagName.toLowerCase());
        
        if (existingTag) {
          // Wenn der Tag bereits existiert, füge ihn zur Liste hinzu
          if (!formData.tags.includes(existingTag.id)) {
            createdTags.push(existingTag);
          }
        } else {
          // Wenn der Tag nicht existiert, erstelle ihn
          try {
            const response = await axios.post(`${API_URL}/api/tags`, { name: tagName });
            const newTag = response.data.data;
            createdTags.push(newTag);
            
            // Aktualisiere die Tags-Liste
            setTags(prevTags => [...prevTags, newTag]);
            setAllTags(prevTags => [...prevTags, newTag]);
          } catch (err) {
            console.error(`Fehler beim Erstellen des Tags "${tagName}":`, err);
          }
        }
      }
      
      // Füge die neuen Tags zur Auswahl hinzu
      setFormData(prev => ({
        ...prev,
        tags: [...new Set([...prev.tags, ...createdTags.map(tag => tag.id)])]
      }));
    };
    
    applyTags();
  };

  const handleApplyCategory = (categoryName: string) => {
    const createCategory = async () => {
      // Prüfe, ob die Kategorie bereits existiert
      const existingCategory = categories.find(c => c.name.toLowerCase() === categoryName.toLowerCase());
      
      if (existingCategory) {
        setFormData(prev => ({ ...prev, categoryId: existingCategory.id }));
      } else {
        try {
          // Erstelle neue Kategorie
          const response = await axios.post(`${API_URL}/api/categories`, { name: categoryName });
          setCategories(prev => [...prev, response.data.data]);
          setFormData(prev => ({ ...prev, categoryId: response.data.data.id }));
        } catch (err) {
          console.error(`Fehler beim Erstellen der Kategorie "${categoryName}":`, err);
        }
      }
    };
    
    createCategory();
  };

  const handleApplyDescription = (description: string) => {
    setFormData(prev => ({
      ...prev,
      description
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    // Validierung
    if (!formData.url.trim() || !formData.title.trim()) {
      setFormErrors({
        url: !formData.url.trim() ? 'Die URL ist erforderlich' : !isValidUrl(formData.url) ? 'Bitte gib eine gültige URL ein' : '',
        title: !formData.title.trim() ? 'Der Titel ist erforderlich' : ''
      });
      return;
    }

    if (!isValidUrl(formData.url)) {
      setFormErrors(prev => ({ ...prev, url: 'Bitte gib eine gültige URL ein' }));
      return;
    }

    setSubmitting(true);
    setError(null);

    try {
      console.log('Speichere Bookmark mit Daten:', formData);
      
      const payload = {
        url: formData.url,
        title: formData.title,
        description: formData.description || undefined,
        categoryId: formData.categoryId || undefined,
        tagIds: formData.tags.length > 0 ? formData.tags : undefined
      };

      console.log('Sende Payload an API:', payload);
      
      // Verwende einen längeren Timeout für die Anfrage
      const response = await axios.post(`${API_URL}/api/bookmarks`, payload, {
        timeout: 30000 // 30 Sekunden Timeout
      });
      
      console.log('Antwort vom Server:', response.data);
      
      setMessage('Bookmark erfolgreich gespeichert!');
      
      // Nach kurzer Verzögerung zur Dashboard-Seite zurückkehren
      setTimeout(() => {
        router.push('/dashboard');
      }, 1500);
    } catch (err: any) {
      console.error('Fehler beim Speichern des Bookmarks:', err);
      
      if (err.code === 'ERR_NETWORK') {
        setError('Netzwerkfehler: Server nicht erreichbar. Bitte prüfen Sie, ob der Server auf Port 8080 läuft.');
      } else if (err.response) {
        setError(err.response?.data?.message || `Fehler (${err.response.status}): ${err.response.statusText}`);
      } else {
        setError('Fehler beim Speichern des Bookmarks: ' + err.message);
      }
    } finally {
      setSubmitting(false);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-indigo-600"></div>
        <p className="ml-3 text-xl text-gray-700">Wird geladen...</p>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 py-12">
      <div className="container mx-auto px-4 max-w-3xl">
        <div className="bg-white shadow-xl rounded-lg overflow-hidden">
          <div className="bg-gradient-to-r from-indigo-600 to-purple-600 px-6 py-4">
            <div className="flex justify-between items-center">
              <h1 className="text-2xl font-bold text-white">Neues Bookmark hinzufügen</h1>
              <button
                onClick={() => router.push('/dashboard')}
                className="px-4 py-2 text-sm text-white bg-white bg-opacity-20 rounded-md hover:bg-opacity-30 transition-all"
              >
                Zurück zum Dashboard
              </button>
            </div>
          </div>

          {message && (
            <div className="m-6 p-4 bg-green-50 border-l-4 border-green-500 text-green-700 rounded">
              <div className="flex">
                <svg className="h-5 w-5 text-green-500 mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M5 13l4 4L19 7" />
                </svg>
                <p>{message}</p>
              </div>
            </div>
          )}

          {error && (
            <div className="m-6 p-4 bg-red-50 border-l-4 border-red-500 text-red-700 rounded">
              <div className="flex">
                <svg className="h-5 w-5 text-red-500 mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12" />
                </svg>
                <p>{error}</p>
              </div>
            </div>
          )}

          <form onSubmit={handleSubmit} className="p-6 space-y-6">
            <div className="space-y-2">
              <label htmlFor="url" className="block text-gray-700 text-sm font-medium">
                URL <span className="text-red-500">*</span>
              </label>
              <div className="relative">
                <input
                  type="text"
                  id="url"
                  name="url"
                  value={formData.url}
                  onChange={handleChange}
                  className={`w-full px-4 py-3 border ${
                    formErrors.url ? 'border-red-500' : 'border-gray-300'
                  } rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all`}
                  placeholder="https://example.com"
                />
                {enrichmentLoading && (
                  <div className="absolute right-3 top-3">
                    <div className="animate-spin rounded-full h-6 w-6 border-t-2 border-b-2 border-indigo-600"></div>
                  </div>
                )}
              </div>
              {formErrors.url && <p className="mt-1 text-sm text-red-500">{formErrors.url}</p>}
            </div>

            <div className="flex items-center mb-4 bg-indigo-50 p-3 rounded-lg">
              <label htmlFor="autoEnrich" className="flex items-center cursor-pointer">
                <div className="relative">
                  <input
                    type="checkbox"
                    id="autoEnrich"
                    className="sr-only"
                    checked={autoEnrichEnabled}
                    onChange={() => setAutoEnrichEnabled(!autoEnrichEnabled)}
                  />
                  <div className={`block w-12 h-6 rounded-full transition-colors ${autoEnrichEnabled ? 'bg-indigo-600' : 'bg-gray-300'}`}></div>
                  <div className={`absolute left-1 top-1 bg-white w-4 h-4 rounded-full transition-transform duration-200 ${autoEnrichEnabled ? 'transform translate-x-6' : ''}`}></div>
                </div>
                <div className="ml-3 text-gray-700 font-medium">
                  Automatische KI-Anreicherung mit GPT-4o mini
                </div>
              </label>
            </div>

            <div className="space-y-2">
              <label htmlFor="title" className="block text-gray-700 text-sm font-medium">
                Titel <span className="text-red-500">*</span>
              </label>
              <input
                type="text"
                id="title"
                name="title"
                value={formData.title}
                onChange={handleChange}
                className={`w-full px-4 py-3 border ${
                  formErrors.title ? 'border-red-500' : 'border-gray-300'
                } rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all`}
                placeholder="Titel des Bookmarks"
              />
              {formErrors.title && <p className="mt-1 text-sm text-red-500">{formErrors.title}</p>}
            </div>

            <div className="space-y-2">
              <label htmlFor="description" className="block text-gray-700 text-sm font-medium">
                Beschreibung
              </label>
              <textarea
                id="description"
                name="description"
                value={formData.description}
                onChange={handleChange}
                rows={4}
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all"
                placeholder="Beschreibe den Inhalt des Bookmarks (optional)"
              />
            </div>

            <div className="space-y-2">
              <label htmlFor="categoryId" className="block text-gray-700 text-sm font-medium">
                Kategorie
              </label>
              <select
                id="categoryId"
                name="categoryId"
                value={formData.categoryId}
                onChange={handleChange}
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all"
              >
                <option value="">Keine Kategorie</option>
                {categories.map(category => (
                  <option key={category.id} value={category.id}>
                    {category.name}
                  </option>
                ))}
              </select>
            </div>

            <div className="space-y-2">
              <label className="block text-gray-700 text-sm font-medium mb-2">
                Tags
              </label>
              <div className="flex flex-wrap gap-2 mb-3 min-h-12 p-2 border border-gray-200 rounded-lg bg-gray-50">
                {allTags.length === 0 ? (
                  <p className="text-gray-500 text-sm">Keine Tags verfügbar. Erstelle unten einen neuen Tag.</p>
                ) : (
                  allTags.map(tag => (
                    <button
                      type="button"
                      key={tag.id}
                      onClick={() => handleTagSelect(tag.id)}
                      className={`px-3 py-1 rounded-full text-sm font-medium transition-all ${
                        formData.tags.includes(tag.id)
                          ? 'bg-indigo-600 text-white shadow-md'
                          : 'bg-gray-100 text-gray-800 hover:bg-gray-200'
                      }`}
                    >
                      {tag.name}
                    </button>
                  ))
                )}
              </div>
              <div className="flex">
                <input
                  type="text"
                  value={newTagName}
                  onChange={(e) => setNewTagName(e.target.value)}
                  placeholder="Neuer Tag"
                  className="flex-1 px-4 py-2 border border-gray-300 rounded-l-lg focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all"
                  onKeyPress={(e) => {
                    if (e.key === 'Enter') {
                      e.preventDefault();
                      handleCreateTag();
                    }
                  }}
                />
                <button
                  type="button"
                  onClick={handleCreateTag}
                  className="px-4 py-2 bg-indigo-600 text-white rounded-r-lg hover:bg-indigo-700 transition-colors"
                >
                  Hinzufügen
                </button>
              </div>
            </div>

            {/* EnrichmentPanel hier einfügen, aber nur wenn URL und Titel ausgefüllt sind */}
            {formData.url && isValidUrl(formData.url) && (
              <EnrichmentPanel
                url={formData.url}
                title={formData.title || 'Unbenannt'}
                description={formData.description}
                onApplyTags={handleApplyTags}
                onApplyCategory={handleApplyCategory}
                onApplyDescription={handleApplyDescription}
              />
            )}

            <div className="pt-4 border-t border-gray-200">
              <button
                type="submit"
                disabled={submitting}
                className="w-full px-6 py-3 bg-gradient-to-r from-indigo-600 to-purple-600 text-white font-medium rounded-lg shadow-md hover:shadow-lg transition-all disabled:opacity-70 disabled:cursor-not-allowed"
              >
                {submitting ? (
                  <div className="flex items-center justify-center">
                    <div className="animate-spin rounded-full h-5 w-5 border-t-2 border-b-2 border-white mr-2"></div>
                    Wird gespeichert...
                  </div>
                ) : (
                  'Bookmark speichern'
                )}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default NewBookmarkPage; 